import * as React from "react";
import {    Icon, Button } from 'antd';
import { withFormik,     FormikProps, Field, Form } from 'formik';
// import { validUserSchema } from "@abb/common";
import { InputField } from "../../shared/InputField";
// import { Link } from "react-router-dom";
import { NormalizedErrorMap, InsertGoalMutationVariables, YearGoalInput } from "@abb/controller";

interface FormValues extends InsertGoalMutationVariables{
  name: string;
  description: string;
  yeargoals: YearGoalInput;
}
interface Props {
    onFinish: () => void;
    submit: (values: FormValues) => Promise<NormalizedErrorMap | null>;
}
class C extends React.PureComponent<FormikProps<FormValues> & Props> {
    
    render() {
       // const { handleSubmit, errors} = this.props;
       // console.log(errors);
    
        return (
          <Form style={{ display: "flex" }}> 
          <div style={{width: 400, margin:'auto'}}>
          {/* <Form onSubmit={this.handleSubmit} className="login-form"> */}
            <Field  
              name="name"
              prefix={
                <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} /> as any
              }
              // placeholder="Name"
              component={InputField}
            />
            <Field  
              name="description"
              prefix={
                <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} /> as any
              }
              // placeholder="설명"
              component={InputField}
            />
            <Field  
              name="yeargoal"
              prefix={
                <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} /> as any
              }
              // placeholder="년목표"
              component={InputField}
            />
       
            {/* <div>
              <Checkbox>Remember me</Checkbox>
                Forgot password
            </div> */}
            <div>
              <Button type="primary" htmlType="submit" className="login-form-button">
                연간 목표값 입력
              </Button>
            </div>
            {/* <div>
              Or <Link to="/login">Login now!</Link>
            </div> */}
          </div>
          </Form>
        );
    }
}





export const InsertGoalView = withFormik<Props, FormValues>({
    // validationSchema: validUserSchema,
    // validateOnChange : false,
    // validateOnBlur: false,
    // mapPropsToValues: () => ({ name: "", description: "", yeargoals: undefined }),

    handleSubmit: async (values, {props, setErrors}) => {
 
        const errors = await props.submit(values);
        if(errors){
            setErrors(errors)
        } else {
          props.onFinish();
        }
    }
})(C);